.demo-btn-copy{font-size:18px}.demo-block{border:1px solid #edf0f7;border-radius:6px;overflow:hidden;margin-bottom:45px}.demo-block .demo-source{text-align:center;padding:40px;overflow:auto}.demo-block .code-bar i{margin-right:4px;font-size:18px}.demo-block .code-bar i,.demo-block .code-bar span{display:inline-block;vertical-align:middle}.demo-desc{border-radius:2px;padding:20px;-webkit-box-sizing:border-box;box-sizing:border-box;border:1px solid #ebebeb;border-radius:3px;font-size:14px;line-height:22px;color:rgba(0,0,0,0.75);word-break:break-word;background-color:#fff}.demo-desc p{margin:0}.demo-desc code{background-color:#e6ebff;padding:2px 5px;margin:4px;border-radius:3px}.demo-highlight{-webkit-transition:height 0.2s ease-out;transition:height 0.2s ease-out;overflow:hidden;background-color:#f8f9fc;border-top:1px solid #edf0f7;padding:20px;font-size:14px}.demo-highlight .highlight>pre{margin:0;padding:0;overflow:auto;-webkit-box-sizing:border-box;box-sizing:border-box}.demo-block-control{position:relative;padding:0px 8px;color:#808ab1;background:#fff;cursor:pointer;text-align:center;border-top:1px solid #edf0f7;line-height:50px}.demo-block-control .mtd-tooltip-rel{float:right;margin-left:12px}.demo-block-control.onfixed{width:958px;position:fixed;bottom:0}.hljs-built_in,.hljs-doctag,.hljs-keyword,.hljs-name,.hljs-section,.hljs-selector-class,.hljs-selector-id,.hljs-selector-tag,.hljs-strong,.hljs-tag,.hljs-title,.hljs-type{color:#5e8ccd}
